.node {
    --node-outline-color: #303F9F;
    --node-active-outline-color: #5161ca; 

    display: flex;
    flex-direction: column;

    height: auto;

    background-color: #1e1e1e;
    color: white;

    margin: 0.25rem;

    border: 2px solid var(--node-outline-color);
    border-radius: 0.25rem;

    text-align: center;
}

.node:active {
    border: 2px solid var(--node-active-outline-color);
}

.node:hover {
    border: 2px solid var(--node-active-outline-color);
}

.node_label {
    display: block;

    padding-top: 0.5rem;

    font-size: 0.75em;
}

.node_preview_label {
    font-size: 0.65em;
}

.node_preview_text {
    margin-top: 0;

    font-size: 0.5em;
}

.node_category {
    background-color: var(--node-outline-color);
  
    padding: 0.1rem 4rem;

    font-size: 0.5em;
}

.node_category:active {
    background-color: var(--node-active-outline-color);
}

.node_category_label {
    pointer-events: none;
}

.node_form {
    text-align: left;
    margin: 0 auto;
}

.node_form_radio {
    display: inline;
    text-align: left;
    transform: scale(0.5);
}

.node_radio_label {
    font-size: 0.5rem;
}

.node_icon_help {
    position: absolute;
    top: 0;
    right: 0;
    padding-top: 0.75rem;
    padding-right: 0.75rem;
}

.tooltip .tooltiptext {
    visibility: hidden;

    position: absolute;
    top: 0.25rem;
    left: 100%;

    padding-top: 0.25rem;
    padding-bottom: 1rem;
    padding-left: 1rem;
    padding-right: 1rem;

    width: 120px;

    background-color: black;
    color: #fff;

    text-align: left;
    font-size: 0.5em;

    border-radius: 1px;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}